/**
 * Class to manage WebGL used in Cubism SDK samples
 */
export class LAppGlManager {
  public constructor() {
    this._gl = null;
  }

  public initialize(canvas: HTMLCanvasElement): boolean {
    // Initialize gl context
    this._gl = canvas.getContext('webgl2');

    if (!this._gl) {
      // gl initialization failed
      alert('Cannot initialize WebGL. This browser does not support.');
      this._gl = null;
      // document.body.innerHTML =
      //   'This browser does not support the <code><canvas></code> element.';
      return false;
    }
    return true;
  }

  /**
   * Release.
   */
  public release(): void {}

  public getGl(): WebGLRenderingContext | WebGL2RenderingContext {
    return this._gl;
  }

  private _gl: WebGLRenderingContext | WebGL2RenderingContext = null;
}