  public Playaudio(audioFileUrl: string): void {
    if (LAppDefine.DebugLogEnable) {
        LAppPal.printMessage(`[APP]audio file: ${audioFileUrl}`);
    }
    
    LAppPal.printMessage(`[APP] Number of models: ${this._models.getSize()}`);
    if (LAppDefine.DebugLogEnable) {
      LAppPal.printMessage(`[APP]audio file: ${audioFileUrl}`);
    }
    const myRequest = new Request(audioFileUrl);
    fetch(myRequest)
        .then((response) => response.blob())
        .then((myBlob) => {
          const url = window.URL.createObjectURL(myBlob);
          const audio = new Audio(url);
            
          // Play the audio file
          audio.play();
          
          // Initialize lip sync for the model
          this._models.at(0)._wavFileHandler.start(url);
          if (LAppDefine.DebugLogEnable) {
            LAppPal.printMessage(`[APP] model at: ${0}`);
          }
        });
  }


  
