  /**
   * Process when updating the screen
   * Performs model update processing and drawing processing
   */
  public onUpdate(): void {
    const { width, height } = this._subdelegate.getCanvas();

    const projection: CubismMatrix44 = new CubismMatrix44();
    const model: LAppModel = this._models.at(0);
    if(!model) return; 

    if (model.getModel()) {
      if (model.getModel().getCanvasWidth() > 1.0 && width < height) {
        // When displaying a horizontally long model in a vertically long window, calculate the scale by the horizontal size of the model
        model.getModelMatrix().setWidth(2.0);
        projection.scale(1.0, width / height);
      } else {
        projection.scale(height / width, 1.0);
      }
      
      // positions of model vertical offset for up and down position -ve for down @@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@
      // Multiply here if necessary
      if (this._viewMatrix != null) {
        projection.multiplyByMatrix(this._viewMatrix);
      }
      const modelWidth = 2.0;
      const canvasAspect = width / height;        
      let horizontalOffset = (canvasAspect - (modelWidth / 2))-0.3;  // increase -ve to move towards center
      let verticalOffset = 0; // default 0   , -0.5 for half,, -0.4 for current models human

      // Apply the offset to the model matrix.  Translate *before* scaling.
      // model.getModelMatrix().translate(horizontalOffset, 0);  horizontal @@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@
      model.getModelMatrix().translate(horizontalOffset, verticalOffset);
    }

    model.update();
    model.draw(projection); // projection is altered because it is passed by reference.
  }

  /**
   * Switch to the next scene
   * In the sample application, the model set is switched.
   */
  public nextScene(): void {
    const no: number = (this._sceneIndex + 1) % LAppDefine.ModelDirSize;
    this.changeScene(no);
  }

  /**
   * Switch scenes
   * In the sample application, the model set is switched.
   * @param index
   */
  private changeScene(index: number): void {
    this._sceneIndex = index;

    if (LAppDefine.DebugLogEnable) {
      LAppPal.printMessage(`[APP]model index: ${this._sceneIndex}`);
    }

    // From the directory name held in ModelDir[]
    // Determine the path of model3.json.
    // Make sure the directory name and the name of model3.json match.
    const model: string = LAppDefine.ModelDir[index];
    const modelPath: string = LAppDefine.ResourcesPath + model + '/';
    let modelJsonName: string = LAppDefine.ModelDir[index];
    modelJsonName += '.model3.json';

    this.releaseAllModel();
    const instance = new LAppModel();
    instance.setSubdelegate(this._subdelegate);
    instance.loadAssets(modelPath, modelJsonName);
    this._models.pushBack(instance);
  }

  public setViewMatrix(m: CubismMatrix44) {
    for (let i = 0; i < 16; i++) {
      this._viewMatrix.getArray()[i] = m.getArray()[i];
    }
  }

  /**
   * Add model
   */
  public addModel(sceneIndex: number = 0): void {
    this._sceneIndex = sceneIndex;
    this.changeScene(this._sceneIndex);
  }

  /**
   * Constructor
   */
  public constructor() {
    this._subdelegate = null;
    this._viewMatrix = new CubismMatrix44();
    this._models = new csmVector<LAppModel>();
    this._sceneIndex = 0;
    this.setupSocketListener();
  }

  public getModel(index: number): LAppModel | null {
    if (index >= 0 && index < this._models.getSize()) {
        return this._models.at(index);
    }
    return null;
  }
  /**
   * Release.
   */
  public release(): void {}

  /**
   * Initialize.
   * @param subdelegate
   */
  public initialize(subdelegate: LAppSubdelegate): void {
    this._subdelegate = subdelegate;
    this.changeScene(this._sceneIndex);
  }

  /**
   * Subdelegate to which it belongs
   */
  private _subdelegate: LAppSubdelegate;

  _viewMatrix: CubismMatrix44; // view matrix used for model drawing
  _models: csmVector<LAppModel>; // Container for model instances
  private _sceneIndex: number; // Index value of the scene to be displayed

  // Callback function for motion playback start
  beganMotion = (self: ACubismMotion): void => {
    LAppPal.printMessage('Motion Began:');
    console.log(self);
  };
  // Callback function for motion playback end
  finishedMotion = (self: ACubismMotion): void => {
    LAppPal.printMessage('Motion Finished:');
    console.log(self);
  };
  // socket  
  private setupSocketListener(): void {
    const socket = io.connect("http://127.0.0.1:5000"); // Use io.connect, not just io
    socket.on("file_url", (data: { url: string }) => {
      console.log("Received file URL:", data.url);
      this.Playaudio(data.url); // Call Playaudio directly on 'this'
    });
  }
